import { Component, OnInit, ViewEncapsulation, ViewChild, ElementRef } from '@angular/core';

import { Router } from '@angular/router';

@Component({
  selector: 'app-invitation',
  templateUrl: './invitation.component.html',
  styleUrls: ['./invitation.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class InvitationComponent implements OnInit {

  @ViewChild('hiddentext') private elementRef: ElementRef;

  constructor(private router: Router) { }

  ngOnInit() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove("body-main");
    body.classList.add("body-invitation");
  }

  GoMain() {
    console.log("goMain");

  }

  ShowMessage() {
    // this.elementRef.nativeElement.focus();
    // this.elementRef.nativeElement.select();
    // document.execCommand('Copy');
    // alert("Copy");
    if (confirm("Do you want to go to the wedding registry?")) {
      window.location.href = 'https://info.ikea-usa.com/giftregistry/en-us/registry/d1f02bba-b0c2-4596-966c-4f54352ceaf9';
    }
  }

  GoPhotoAlbum() {
    if (confirm("It's going to the photo album.")) {
      this.router.navigate(['/Main']);
    }
  }

  CopyAddress(){
    this.elementRef.nativeElement.focus();
    this.elementRef.nativeElement.select();
    document.execCommand('Copy');
    alert("3198 Hamilton Mill Rd, Buford, GA 30519 copied");
  }

  // window.location.href = 'https://info.ikea-usa.com/giftregistry/en-us/registry/d1f02bba-b0c2-4596-966c-4f54352ceaf9';
}



