import { Component, Input, OnInit, Inject, ViewChild, ElementRef, EventEmitter, ViewEncapsulation } from '@angular/core';
// Can also be included with a regular script tag
// import { Typed } from 'typed.js';
// import { Typed } from './typed';
import { SwiperComponent } from 'angular2-useful-swiper';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class MainComponent implements OnInit {
  title = 'app';
  config: SwiperOptions = {
    pagination: '.swiper-pagination',
    paginationClickable: true,
    nextButton: '.swiper-button-next',
    prevButton: '.swiper-button-prev',
    spaceBetween: 30,
    autoplay: 3000
  };

  @ViewChild('typetext') private elementRef: ElementRef;

  ngOnInit() {
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove("body-invitation");
    body.classList.add("body-main");
  }

  constructor() {
  }


  onTypingAnimationComplete() {
    console.log('#TYPING ANIMATION COMPLETE');
  }

}






